.contactsBlock {
    height: 80vh;
    display: flex;
    justify-content: center;
    align-items: center;
    background-image: url("../common/contact-bg.jpg");
    background-attachment: fixed;
    background-color: black;
    background-position: center;
    background-repeat: no-repeat;
    position: relative;
    padding: 120px 0 120px 0;
}


.contactsContainer {
    justify-content: space-around;
    flex-direction: column;
    position: absolute;
}

.contactsBlock img {
    max-width: 100%;
    opacity: 0.5;
}

.title {
    border-bottom: 4px solid #109af7;
    border-top: 4px solid #000;
    display: inline-block;
    font-size: 36px;
    color: #222;
    margin-bottom: 15px;
    margin-top: 0;
    padding: 5px 0;
}

.contactsBlock h4{
    color: white;
}

.form {
    max-width: 800px;
    height: 230px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.form input {
    padding: 15px 20px;
    background-color: rgba(255, 255, 255, .1);
    border: 0 none;
    color: #E4E0EB;
    height: auto;
}

input::placeholder{
    color: white;
}

.form textarea {
    padding: 15px 20px;
    background-color: rgba(255, 255, 255, .1);
    border: 0 none;
    color: #E4E0EB;
}

input {
    height: 40px;
}

textarea {
    height: 100px;
}

.contactsBlock button {
    background-color: transparent;
    border: 2px solid #fff;
    border-radius: 1px;
    color: #fff;
    font-size: 16px;
    font-weight: 400;
    padding: 15px 20px;
    text-transform: uppercase;
    -webkit-transition: .3s;
    transition: .3s;
}

.contactsBlock button:hover {
    background: rgba(0,0,0,0) -webkit-linear-gradient(left,#00c6ff,#0072ff);
    border-color: #109af7;
    color: #fff;
}

@media screen and (max-width: 768px) {
    .form input {
        width: 80vw;
    }
    .form textarea{
        width: 80vw;
    }
    .form{
        align-items: center;
    }
    .contactsBlock button{
        font-size: 14px;
        padding: 10px;
    }
}
